import uniRate from '../../components/uni-ui/uni-rate/uni-rate.vue'
export default {
	name: "",
	components: {
		uniRate
	},
	props: {},
	data() {
		return {
			slideshow: [], //轮播图
			HomeData: [], //首页数据
			trailer: [], //预告
			love: [], //猜你喜欢
		}
	},
	methods: {
		//获取轮播图
		getslideshow() {
			uni.request({
				url: `${this.$api}/index/carousel/list?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.slideshow = res.data.data;
					// console.log(this.slideshow)
					// console.log(res)
					this.getHomeData()
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取首页数据
		getHomeData() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=superhero`,
				method: 'POST',
				data: {},
				success: res => {
					this.HomeData = res.data.data;
					// console.log(this.HomeData)
					this.getHomeDataPrevue()
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//获取热门预预告
		getHomeDataPrevue() {
			uni.request({
				url: `${this.$api}/index/movie/hot?qq=122212489&type=trailer`,
				method: 'POST',
				data: {},
				success: res => {
					this.trailer = res.data.data;
					// console.log(this.trailer)
					this.getlove()
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//猜你喜欢 随机获取预告
		getlove() {
			uni.request({
				url: `${this.$api}/index/guessULike?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.love = res.data.data;
					console.log(res)

				},
				fail: () => {},
				complete: () => {}
			});
		},
		//赞一下
		toZan(){
			uni.showToast({
			    title: '已点赞',
			    duration: 2000
			});
		},
	},
	mounted() {

	},
	onLoad() {
		this.getslideshow()
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
